import React, { Component } from 'react';
import { faFacebook, faInstagram } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
class Footer extends Component {
    render() {
        return (
            <div className="footer center" id="footer">
                <div className="">
                    <a href="https://www.facebook.com/P16334" style={{ fontSize: "x-large", color: "#4267B2" }}>
                        <FontAwesomeIcon icon={faFacebook} />
                    </a>
                    <a className="text-dark" href="https://www.instagram.com/9_pyra/" style={{ fontSize: "x-large" }}>
                        <FontAwesomeIcon icon={faInstagram} />
                    </a>
                    <p className="center">• 15 March 2020</p>
                </div>
            </div>
        )
    }
}
export default Footer;