import React, { Component } from 'react';
import { Route, Switch, NavLink } from 'react-router-dom';
import Home from './container/Home/Home';
import About from './container/About/About';
import './index.css';


window.onscroll = function () {
    const winScroll = document.body.scrollTop || document.documentElement.scrollTop;
    const logo = document.querySelector("#logo");
    const home = document.querySelector("#homeContent");
    const footer = document.querySelector("#homeFooter");

    // document.getElementById("test").innerHTML = winScroll;

    effectActive("head-1", 200, "slideUp");//(id,location)
    effectActive("content-1", 300, "slideUp");
    effectActive("content-2", 390, "slideUp");

    if (home !== null) {
        home.style.bottom = footer.style.bottom = `${Math.ceil(winScroll / 2)}px`;
    }
    logo.style.transition = "1s";
    logo.style.boxShadow = winScroll <= 10 ? "0px 15px 15px #00000067" : "unset";
};

function effectActive(id, location, className) {
    const winScroll = document.body.scrollTop || document.documentElement.scrollTop;
    const effectActive = document.getElementById(`${id}`);
    className = (className === null || className === "" || className === undefined) ? "slideUp" : className;
    if (winScroll > location && effectActive !== null) {
        effectActive.style.visibility = "visible";
        effectActive.classList.add(className);
    } else if (winScroll <= (location - 190) && effectActive !== null) {
        effectActive.classList.remove(className);
        effectActive.style.visibility = "hidden";
    }
}

class App extends Component {
    // ! How to create constructor
    // constructor(props) {
    //     super(props);
    //     this.state = { title: '' };
    // }

    render() {
        return (
            <div className="app">
                {/* <p id="test" className="col" style={{ position: "fixed", zIndex: 200, top: "0px" }}></p> */}
                <div className="nav-bar start" id="nav">
                    <div className="col-1 left center">
                        <p id="logo" className="btn large btn-float light">
                            Logo
                        </p>
                    </div>
                    <div className="col-2 right">
                        <NavLink exact to='/' activeClassName="is-active" className="btn round btn-bloat" id="home">Home</NavLink>
                        <NavLink to='/about' activeClassName="is-active" className="btn round btn-bloat" id="about">About</NavLink>
                    </div>
                </div>
                <div className="body">
                    <Switch>
                        <Route exact path='/' component={Home} />
                        <Route path='/about' component={About} />
                    </Switch>
                </div>
            </div >
        )
    }
}
export default App;