import React, { Component } from "react";
import Footer from '../../component/Footer';
// import content from './content.json';
import axios from 'axios';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = { content: [] };
  }
  componentDidMount() {
    document.title = 'Privy • Home';

    axios.get('https://privy-server.herokuapp.com/contents/')
      .then((res) => this.setState({ content: res.data }));
  }
  render() {
    const forums1 = this.state.content[0] !== undefined ? this.state.content[0].forums : "";
    const forums2 = this.state.content[1] !== undefined ? this.state.content[1].forums : "";

    return (
      <div className="row">
        <div className="card row" style={{ background: "#015077" }}>
          <div className="col center">
            <div className="star" id="star" />
          </div>
          <div className="col center">
            <div className="meteor" id="meteor" />
          </div>
          <div className="sec-moon">
            <div className="moon" id="moon">
              <div className="dmoon dmoon-0"></div>
              <div className="dmoon dmoon-1"></div>
              <div className="dmoon dmoon-2"></div>
              <div className="dmoon dmoon-3"></div>
              <div className="dmoon dmoon-4"></div>
              <div className="dmoon dmoon-5"></div>
            </div>
          </div>
          <div className="sec-mountain-1 col-6 center" id="sec-m-1">
            <div className="mountain m-1" id="m-1">
              <div className="cap" id="cap-1">
                <div className="dcap dcap-1" />
                <div className="dcap dcap-2" />
                <div className="dcap dcap-3" />
              </div>
            </div>
            <div className="mountain-2 m-2" id="m-2">
              <div className="cap2" id="cap-2">
                <div className="dcap2 dcap2-1" />
                <div className="dcap2 dcap2-2" />
                <div className="dcap2 dcap2-3" />
                <div className="dcap2 dcap2-4" />
              </div>
            </div>
            <div className="mountain-3 m-3" id="m-3">
              <div className="cap3" id="cap-3">
                <div className="dcap3 dcap3-0" />
                <div className="dcap3 dcap3-1" />
                <div className="dcap3 dcap3-2" />
                <div className="dcap3 dcap3-3" />
              </div>
            </div>
            <div className="sec-cloud">
              <div className="cloud cloud-1"> </div>
              <div className="cloud cloud-2" />
            </div>
          </div>
          <div id="sea-1" className="sec-sea col center">
            <div className="wave" style={{ top: "-10%", left: "50%" }} />
            <div
              className="wave"
              style={{ top: "20%", left: "25%", animationDelay: "1s" }}
            />
            <div
              className="wave"
              style={{ top: "-5%", left: "0%", animationDelay: "2s" }}
            />
            <div
              className="wave"
              style={{ top: "10%", left: "-25%", animationDelay: "3s" }}
            />
            <div className="wave" style={{ top: "10%", left: "-50%" }} />
            <div
              className="wave"
              style={{ top: "-80%", left: "-50%", animationDelay: "3s" }}
            />
          </div>
        </div>
        <div id="homeContent" className="card-content dark">
          <div className="col">
            <p className="large center" id="head-1">Content</p>
            <div className="medium center start">
              <p className="left col-2" id="content-1">
                <br />&emsp;&emsp;{forums1}
              </p>
              <p className="left col-2" id="content-2">
                <br />&emsp;&emsp;{forums2}
              </p>
            </div>
          </div>
        </div>
        <div id="homeFooter" className="card-content row-1">
          <div className="col">
            <Footer />
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
